const description = `La pièce Le confessionnal a pour but de premier de susciter des réflexions sur l’anxiété que peut provoquer le passage à l’âge adulte. « Qu’est-ce que je vais faire de ma vie? » « Est-ce que mes parents seront fiers de moi? » « Pourrai-je être enfin celle ou celui que je suis vraiment après le secondaire? » On se pose tant de questions sur le chemin de la quête de soi. Parfois, cette quête de soi peut être douloureuse, pouvant créer chez certains des troubles anxieux ou autres troubles mentaux. 
L’expérience de la pièce Le confessionnal est en soi un outil pour amorcer une discussion et parler des ressources aux élèves qui en ont besoin. Par le médium du théâtre, les étudiants pourront mieux aller à la rencontre de leurs émotions et de ce qu'ils ressentent par rapport à leurs angoisses face à l'avenir. C’est ce que nous voulons offrir aux étudiants : Une ouverture sur ce qu’ils ressentent, sans jugement; un début de cheminement pour mettre un terme aux tabous vis-à-vis la santé mentale.
`;

export default description;


